@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
  box-sizing: border-box;
}

body {
  background-color: #f9fafb;
  font-family: 'Open sans', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  background: linear-gradient(
    to left,
    rgb(86, 204, 155) 0%,
    rgb(210, 211, 140) 100%
  );
}
